import {useState, useEffect} from 'react';
import {getPostBySlug} from '../modules/fetches';
import {API_URL} from '../modules/constants';
import {truncate} from 'lodash';
export default (props) => {
  const [post, setPost] = useState([])
  
  useEffect(()=>{
    let ignore = false;
    const slug = props.url.split('/').pop();

    const getPost = async (slug)=> {
      const response = await getPostBySlug(slug)
      if (!ignore) setPost(response.data.post);
    }

    getPost(slug)
    return () => { ignore = true; }

  },[])

  return (<div className="preview__container">
    <img className="preview__image" src={`${API_URL}/images/${post.image}`}></img>
    <p className="preview__title">{post.title}</p>
    <p className="preview__description">{truncate(post.content,{length: 140})}</p>

  </div>)
}